import React from "react";
import './DeckStatistics.css';

export default function DeckStatistics({cardsCount, gamesCount, userRating, lastRepeatTime}) {
    const playedBefore = isOk(gamesCount) && gamesCount > 0;
    return (
        <div className='deck-statistics'>
            <div>–ö–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ –∫–∞—Ä—Ç: {cardsCount}</div>
            {
                isOk(gamesCount) &&
                <div>–ö–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ –∏–≥—Ä: {gamesCount}</div>
            }
            {
                isOk(userRating) && playedBefore &&
                <div>–í–∞—à —Ä–µ–π—Ç–∏–Ω–≥: {Math.round(userRating * 100)}%</div>
            }
            {
                isOk(lastRepeatTime) && playedBefore &&
                <div>–ü–æ—Å–ª–µ–¥–Ω—è—è —Ç—Ä–µ–Ω–∏—Ä–æ–≤–∫–∞: {getLastRepeatTime(lastRepeatTime)}</div>
            }
        </div>
    );
}

const isOk = value => value !== undefined;

const getLastRepeatTime = date => {
    const seconds = Math.floor((new Date() - date) / 1000);

    let interval = Math.floor(seconds / 60 / 60 / 24 / 30);
    if (interval >= 1)
        return '–ë–æ–ª—å—à–µ –º–µ—Å—è—Ü–∞ –Ω–∞–∑–∞–¥';

    interval = Math.floor(seconds / 60 / 60 / 24 / 7);
    if (interval > 1)
        return `${interval} –Ω–µ–¥–µ–ª–∏ –Ω–∞–∑–∞–¥`;
    if (interval === 1)
        return '–ù–µ–¥–µ–ª—é –Ω–∞–∑–∞–¥';

    interval = Math.floor(seconds / 60 / 60 / 24);
    if (interval > 5)
        return `${interval} –¥–Ω–µ–π –Ω–∞–∑–∞–¥`;
    if (interval > 1)
        return `${interval} –¥–Ω—è –Ω–∞–∑–∞–¥`;
    if (interval === 1)
        return '–í—á–µ—Ä–∞';

    return '–°–µ–≥–æ–¥–Ω—è';
};
